@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  background-color: #0a0c0f;
  color: #f9fdff;
}

.app {
  height: 100vh;
  max-width: 120rem;
  display: grid;
  align-content: center;
  margin: 0 auto;
  gap: 6.4rem;
}

.app button {
  background-color: #f9fdff;
  width: 7.2rem;
  height: 7.2rem;
  font-size: 4.8rem;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  justify-self: center;
  cursor: pointer;
  transition: all 0.2s ease-in;
}

.app button:hover {
  background-color: #ffc94a;
  transform: translateY(-3px);
}

.cards-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 15rem;
  gap: 4.8rem;
  font-size: 3.2rem;
}

.cards-container div {
  padding: 1.6rem 3.2rem;
  border: 1px solid #f9fdff;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease-in;
}

.cards-container div:hover {
  border: 1px solid #ffc94a;
}

.cards-container div.selected {
  background-color: #f9fdff;
  color: #0a0c0f;
  border: 1px solid #f9fdff;
}
